@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;700&display=swap');
* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}

nav {
    background-color: rgb(75, 75, 110);
    padding: 25px 0;
    color: #f1f1f1;
    font-weight: 800;
    text-align: center;
}

.header {
    text-align: center;
    padding: 20px 0;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.header>a {
    text-decoration: none;
    color: #000;
    font-weight: 600;
    margin: 0 20px;
}

.container {
    background-color: #f1f1f1;
    width: 70%;
    margin: 0 auto;
    min-height: 100vh;
    padding: 40px 80px;
    margin-top: 30px;
}

.container>h1 {
    margin-bottom: 15px;
    text-align: center;
}

.container>form {
    display: flex;
    flex-direction: column;
}

input,
textarea,
button {
    padding: 20px;
    border: 1px solid rgb(189, 174, 174);
    border-radius: 5px;
    margin-top: 10px;
}

button {
    border: none;
    background-color: rgb(75, 75, 110);
    color: #fff;
    font-weight: 600;
    cursor: pointer;
}

.task {
    display: flex;
    background-color: #fff;
    border-radius: 10px;
    padding: 20px;
    margin: 20px 0;
    display: flex;
    align-items: center;
}

.task div {
    width: 100%;
}

.task button {
    width: 30px;
    height: 30px;
    margin: auto;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100%;
    background-color: red;
}

.about {
    height: 100vh;
    background-color: rgb(160, 55, 55);
}

.contact {
    height: 100vh;
    background-color: rgb(139, 139, 68);
}

.user {
    height: 100vh;
    background-color: rgb(224, 88, 88);
}

.footer {
    height: 200px;
    background-color: #000;
}